@import "../../helpers/variables";
@import "../../helpers/mixins";

.reviews {
  margin: $level-2 0;
  @include adaptive(1366) {
    margin: $level-3 0;
  }
  &__wrapper {
    @include adaptive(768) {
      display: flex;
      flex-direction: column;
    }
  }
  &__slider {
    margin-top: $level-4;
    @include adaptive(768) {
      order: 3;
      width: 100%;
    }
    &_item {
      border: 1px solid $gray;
      padding: $level-4 32px;
      display: flex;
      flex-wrap: wrap;
      height: auto;
      @include adaptive(1600) {
        padding: $level-5 32px;
      }
    }
    &_image {
      width: 150px;
      display: flex;
      margin-right: $level-6;
      margin-bottom: $level-6;
      align-items: flex-start;
      @include adaptive(1600) {
        width: 110px;
      }
      @include adaptive(768) {
        width: 150px;
        margin-right: 30px;
      }
      @include adaptive(420) {
        width: 100%;
        height: 210px;
      }
      & img {
        width: auto;
        height: auto;
        max-height: 100%;
      }
      & + .reviews__slider_info {
        width: calc(100% - 175px);
        @include adaptive(1600) {
          width: calc(100% - 135px);
        }
        @include adaptive(768) {
          width: calc(100% - 195px);
        }
        @include adaptive(420) {
          width: 100%;
        }
      }
    }
    &_info {
      width: 100%;
      display: flex;
      flex-direction: column;
    }
    &_header {
      border-left: 4px solid $orange;
      padding-left: 16px;
      margin-bottom: $level-6;
    }
    &_date {
      color: $gray-2;
    }
    &_text {
      margin-bottom: $level-6;
    }
    &_link {
      margin-top: auto;
      &:hover {
        text-decoration: none;
      }
    }
  }
  &__controls {
    position: absolute;
    right: 0;
    top: 0;
    height: 36px;
    display: flex;
    @include adaptive(768) {
      position: relative;
      order: 2;
      top: $level-5;
    }
  }
}
