@import "../helpers/variables";
@import "../helpers/mixins";

.input {
  border: none;

  &-search {
    width: 100%;
    height: 48px;
    background: #f2f2f2;
    padding: 0 48px 0 20px;
    transition: $time;
    @include placeholder {
      user-select: none;
      color: $gray-2;
      @include adaptive(768) {
        font-size: 0;
      }
    }
    @include adaptive(1600) {
      height: 36px;
      padding: 0 36px 0 16px;
    }
    @include adaptive(1200) {
      padding: 0 28px 0 16px;
    }
  }

  &-text {
    background: none;
    padding-bottom: $level-8;
    border-bottom: 1px solid $black;
    font-size: 14px;
    @include placeholder {
      color: $black;
    }
  }
}
