@import "../helpers/variables";
@import "../helpers/mixins";
@import "../helpers/extenders";

body,
html {
  margin: 0;
  padding: 0;
  min-height: 100%;
}

img {
  max-width: 100%;
}

*,
*:after,
*:before {
  box-sizing: border-box;
  outline: none;
  -webkit-font-smoothing: antialiased;
}

* {
  position: relative;
}

*::selection {
  background: $orange;
}

body {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 160%;
  overflow-x: hidden;
  min-width: 320px;
  min-height: 100vh;
  color: $black;
  @include adaptive(1600) {
    font-size: 12px;
  }
}

::-webkit-scrollbar {
  -webkit-appearance: none;
}

::-webkit-scrollbar:vertical {
  width: 12px;
}

::-webkit-scrollbar:horizontal {
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: $dark-blue;
  border-radius: 0;
  border: 0;
  transition: all .2s ease-in-out;
}

::-webkit-scrollbar-track {
  background-color: $gray;
  transition: all .2s ease-in-out;
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
  display: none;
}

a,
label {
  -webkit-tap-highlight-color: transparent;
}

h1,
.h1 {
  font-family: 'Oswald', sans-serif;
  font-weight: 500;
  font-size: 30px;
  line-height: 140%;
  letter-spacing: .1em;
  text-transform: uppercase;
  color: inherit;
  margin: 0 0 $level-7;
  @include adaptive(1600) {
    font-size: 26px;
  }
}

h2,
.h2 {
  font-family: 'Oswald', sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 130%;
  letter-spacing: .1em;
  text-transform: uppercase;
  color: inherit;
  @include adaptive(1600) {
    font-size: 20px;
  }
}

h3,
.h3 {
  font-family: 'Oswald', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: .1em;
  text-transform: uppercase;
  color: inherit;
  @include adaptive(1600) {
    font-size: 16px;
  }
}

h4,
.h4 {
  font-family: 'PT Sans', sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  color: inherit;
  @include adaptive(1600) {
    font-size: 14px;
  }
}

a {
  color: $orange;
  font-weight: 700;
  text-decoration: none;
  transition: $time;

  &:hover {
    text-decoration: underline;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  margin: -1px;
  position: absolute;
  clip: rect(0 0 0 0);
}

.container {
  @extend %container;
  max-width: 1470px;
  box-sizing: content-box;
  @include adaptive(1600) {
    padding: 0 10px;
    max-width: 1180px;
  }
  @include adaptive(1200) {
    max-width: 950px;
  }
  @include adaptive(1024) {
    max-width: calc(100% - 50px);
    padding: 0 25px;
  }
  @include adaptive(600) {
    max-width: calc(100% - 36px);
    padding: 0 18px;
  }
}

header,
main,
footer {
  flex-shrink: 0;
}

main {
}

footer {
  margin-top: auto;
}

.fancybox-navigation {
  position: static;
}

.overlay {
  background: $dark-gray-transparent;
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 50;
  opacity: 1;
  display: none;
}

.scroll-up {
  position: fixed;
  bottom: $level-1;
  right: calc(25vw - 365px);
  z-index: 90;
  cursor: pointer;
  padding-top: 44px;
  opacity: 0;
  pointer-events: none;
  transition: $time;
  @include adaptive(1600) {
    right: calc(25vw - 301px);
    bottom: $level-3;
  }
  @include adaptive(1200) {
    right: calc(25vw - 244px);
  }
  @include adaptive(1024) {
    right: 25px;
    bottom: 160px;
  }
  @include adaptive(768) {
    right: 19px;
    bottom: 74px;
  }
  &._visible {
    opacity: 1;
    pointer-events: initial;
  }
  & .icon {
    color: $orange;
    font-size: 12px;
    transform: rotate(90deg);
    position: absolute;
    top: 12px;
    height: 12px;
  }
  & svg {
    fill: $gray-2;
    transition: $time;
  }
  &:hover {
    & .icon {
      top: 0;
      &::before {
        content: $icon-long-arrow;
        font-size: 24px;
      }
    }
  }
  &:active {
    & svg {
      fill: $dark-gray;
    }
  }
}
