@import "../../helpers/variables";
@import "../../helpers/mixins";

.partners {
  margin: $level-2 0;
  @include adaptive(1366) {
    margin: $level-3 0;
  }
  &__wrapper {
    @include adaptive(768) {
      display: flex;
      flex-direction: column;
    }
  }
  &__slider {
    background: #f2f2f2;
    margin-top: $level-4;
    @include adaptive(768) {
      order: 3;
      width: 100%;
    }
    &_item {
      padding: $level-4 $level-7;
      display: flex;
      align-items: center;
      justify-content: center;
      height: auto;
      filter: grayscale(1);
      transition: $time;
      &:hover {
        filter: grayscale(0);
      }
    }
  }
  &__controls {
    display: flex;
    position: absolute;
    right: 0;
    top: 0;
    height: 36px;
    @include adaptive(768) {
      position: relative;
      order: 2;
      top: $level-5;
    }
  }
}
