@import "../../helpers/variables";
@import "../../helpers/mixins";

.feedback {
  margin-top: $level-2;
  @include adaptive(1366) {
    margin-top: $level-3;
  }
  @include adaptive(768) {
    display: flex;
    flex-direction: column;
  }
  &__container {
    pointer-events: none;
  }
  &__wrapper {
    width: calc(50% - 15px);
    height: 574px;
    background: $orange;
    padding: $level-3 95px;
    pointer-events: initial;
    @include adaptive(1600) {
      padding: $level-4 80px;
      width: calc(50% - 10px);
      height: 480px;
    }
    @include adaptive(1200) {
      width: calc(50% - 5px);
      height: 499px;
      padding: $level-4 70px;
    }
    @include adaptive(768) {
      width: 100%;
      height: 445px;
      padding: $level-5;
      order: 1;
    }
    @include adaptive(600) {
      height: 464px;
    }
    @include adaptive(420) {
      height: 502px;
    }
    @include adaptive(420) {
      height: 557px;
    }
    @include adaptive(360) {
      height: 576px;
    }
  }
  &__form {
    &_header {
      margin-bottom: 20px;
    }
    &_text {
      padding-right: 60px;
      @include adaptive(600) {
        padding-right: 0;
      }
    }

    &_button {
      width: 220px;

    }
  }
  &__map {
    width: 100%;
    height: 762px;
    position: absolute;
    bottom: 0;
    display: block;
    @include adaptive(1600) {
      height: 660px;
    }
    @include adaptive(1366) {
      height: 620px;
    }
    @include adaptive(1024) {
      height: 640px;
    }
    @include adaptive(768) {
      position: relative;
      height: 445px;
      order: 2;
    }
    @include adaptive(600) {
      height: 300px;
    }
  }
}
