@import "../../helpers/variables";
@import "../../helpers/mixins";

.banner {
  height: calc(100vh - 157px);
  min-height: 40vw;
  @include adaptive(1600) {
    height: calc(100vh - 118px);
  }
  @include adaptive(1200) {
    height: calc(100vh - 118px);
  }
  @include adaptive(1024) {
    height: calc(50vw + 122px);
  }
  @include adaptive(768) {
    height: calc(100vh - 64px);
  }
  @include adaptive(1024) {
    @include adaptiveHeight(1024) {
      height: calc(100vh - 64px);
    }
    @include adaptiveHeight(768) {
      height: 100vh;
    }
  }
  &__wrapper {
    height: 100%;
  }
  &__slide {
    height: 100%;
    &_image {
      display: flex;
      width: 100%;
      height: 100%;
      & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      &::after {
        content: '';
        width: 100%;
        height: 100%;
        background: $black;
        opacity: .6;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    &_info {
      position: absolute;
      width: 100%;
      height: calc(100% - 194px);
      top: 0;
      left: 0;
      color: $white;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      @include adaptive(1600) {
        height: calc(100% - 141px);
      }
      @include adaptive(1024) {
        height: calc(100% - 78px);
        justify-content: flex-start;
        padding-top: $level-2;
      }
      @include adaptive(768) {
        justify-content: center;
      }
      @include adaptive(600) {
        justify-content: flex-start;
      }
      @include adaptive(1024) {
        @include adaptiveHeight(380) {
          padding-top: $level-4;
        }
      }
    }
    &_header {
      font-family: "Oswald", sans-serif;
      line-height: 110%;
      font-weight: 500;
      font-size: 48px;
      letter-spacing: .1em;
      text-transform: uppercase;
      margin-bottom: $level-7;
      max-width: 595px;
      @include adaptive(1600) {
        font-size: 40px;
      }
      @include adaptive(600) {
        font-size: 30px;
      }
    }
    &_subheader {
      color: $white;
      margin-bottom: $level-7;
      max-width: 595px;
      @include adaptive(600) {
        font-size: 16px;
      }
    }
    &_text {
      font-family: "PT Sans", sans-serif;
      font-size: 16px;
      line-height: 155%;
      margin-bottom: $level-4;
      max-width: 595px;
      @include adaptive(1024) {
        font-size: inherit;
        margin-bottom: 32px;
      }
    }
    &_link {
      display: flex;
      width: 220px;
      height: 64px;
      margin-bottom: $level-1;
      @include adaptive(1600) {
        margin-bottom: $level-3;
        height: 48px;
      }
      @include adaptive(768) {
        width: 153px;
      }
      &:hover {
        text-decoration: none;
      }
    }
  }
  &__controls {
    bottom: 180px;
    display: flex;
    @include adaptive(1600) {
      bottom: 127px;
    }
    @include adaptive(1024) {
      bottom: $level-3;
    }
    @include adaptive(600) {
      bottom: $level-4;
    }
  }
  &__control {
    color: $white;
  }
}
