@import "../helpers/variables";
@import "../helpers/mixins";

.header {
  &__wrapper {
    display: flex;
  }

  &__logo {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    width: 220px;
    margin-right: 30px;
    user-select: none;
    @include adaptive(1600) {
      width: 180px;
      margin-right: 20px;
    }
    @include adaptive(1200) {
      width: 150px;
      margin-right: 10px;
    }
    @include adaptive(1024) {
      width: calc(20% - 16px);
      margin-right: 20px;
    }

    & img {
      @include adaptive(1600) {
        max-height: 94px;
        width: auto;
      }
      @include adaptive(1200) {
        max-height: 80px;
      }
      @include adaptive(1024) {
        max-height: 36px;
      }
      @include adaptive(768) {
        max-height: 40px;
      }
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__top {
    height: 88px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include adaptive(1600) {
      height: 72px;
    }
    @include adaptive(1024) {
      height: 64px;
      padding-right: 51px;
    }
    @include adaptive(768) {
      padding-right: 41px;
      justify-content: flex-end;
    }
    @include adaptive(600) {
      padding-right: 48px;
    }
    & > .header__contacts {
      @include adaptive(1024) {
        display: none;
      }
    }
    & > .header__socials {
      @include adaptive(1024) {
        display: none;
      }
    }
  }

  &__search {
    width: 345px;
    @include adaptive(1600) {
      width: 180px;
    }
    @include adaptive(1200) {
      width: 150px;
    }
    @include adaptive(1024) {
      width: calc(50% - 35.5px);
      margin-right: 20px;
    }
    @include adaptive(768) {
      margin-right: 0;
      height: 100%;
    }

    &:hover {
      & .header__search_button {
        color: $orange;
      }
    }

    &_input {
      @include adaptive(768) {
        height: 64px;
        width: 100vw;
        position: absolute;
        top: 64px;
        right: -110px;
        z-index: 10;
        transform: translateX(100%) scaleX(0);
      }
      &.input-active {
        @include adaptive(768) {
          transform: translateX(0) scaleX(1);
        }
        & + .header__search_button {
          color: $orange;
        }
      }
    }

    &_button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 100%;
      background: transparent;
      border: none;
      font-size: 20px;
      position: absolute;
      top: 0;
      right: 0;
      cursor: pointer;
      transition: $time;
      @include adaptive(1600) {
        width: 36px;
        font-size: 16px;
      }
      @include adaptive(1200) {
        width: 28px;
        font-size: 14px;
      }
      @include adaptive(768) {
        font-size: 20px;
      }
    }
  }

  &__contacts {
    display: flex;
    flex-wrap: wrap;
    text-align: right;
    max-width: 425px;
    @include adaptive(1600) {
      max-width: 320px;
    }
    @include adaptive(1024) {
      width: 100%;
      flex-direction: column;
      text-align: center;
      border-top: 1px solid #d2d2d8;
      margin-top: auto;
      padding-top: 32px;
    }

    &_time {
      width: 50%;
      @include adaptive(1600) {
        width: 55%;
      }
      @include adaptive(1024) {
        display: none;
      }
    }

    &_address {
      width: 50%;
      order: 2;
      @include adaptive(1600) {
        width: 55%;
      }
      @include adaptive(1024) {
        display: none;
      }
    }

    &_phone {
      display: block;
      width: 50%;
      color: $black;
      @include adaptive(1600) {
        width: 45%;
      }
      @include adaptive(1024) {
        width: 100%;
      }
    }

    &_email {
      display: block;
      width: 50%;
      order: 2;
      font-weight: 400;
      color: $black;
      @include adaptive(1600) {
        width: 45%;
      }
      @include adaptive(1024) {
        width: 100%;
      }
    }
  }

  &__socials {
    display: flex;
    @include adaptive(1024) {
      margin-top: $level-6;
    }

    &_link {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      width: 40px;
      height: 40px;
      background: #f2f2f2;
      color: $black;
      border-radius: 50%;
      margin: 4px;
      @include adaptive(1600) {
        margin: 2px;
        width: 34px;
        height: 34px;
      }

      &:hover {
        text-decoration: none;
        color: $orange;
      }

      &:active {
        color: $orange;
        background: $gray;
      }

      &.icon-whatsapp {
        font-size: 16px;
        @include adaptive(1600) {
          font-size: 14px;
        }
      }

      &.icon-facebook {
        font-size: 13px;
        @include adaptive(1600) {
          font-size: 12px;
        }
      }

      &.icon-instagram {
        font-size: 15px;
        @include adaptive(1600) {
          font-size: 13px;
        }
      }
    }
  }

  &__nav {
    border-top: 1px solid $gray;
    padding: 20px 0 $level-6;
    display: flex;
    @include adaptive(1600) {
      padding: $level-7 0;
    }
    @include adaptive(1024) {
      border: none;
      background: $gray;
      position: absolute;
      right: -25px;
      top: 64px;
      z-index: 100;
      width: 320px;
      height: calc(100vh - 64px);
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      box-shadow: 0 2px 14px rgba(0, 0, 0, .05);
      padding: 32px 0 110px;
      transform: translateX(100%);
      transition: $time;
      overflow-y: scroll;
      &::-webkit-scrollbar:vertical {
        width: 6px;
        z-index: 130;
      }
    }
    @include adaptive(600) {
      right: -18px;
      width: 100vw;
    }
    @include adaptive(1024) {
      @include adaptiveHeight(420) {
        height: 100vh;
        top: 0;
        padding: $level-4 0;
      }
    }
    &_wrap {
      display: flex;
      justify-content: space-between;
      width: 100%;
      @include adaptive(1024) {
        height: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
    &_link {
      color: $black;
      font-family: "Oswald", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      letter-spacing: .1em;
      text-transform: uppercase;
      padding-left: 6px;
      @include adaptive(1600) {
        font-size: 14px;
      }
      @include adaptive(1200) {
        font-size: 12px;
      }
      @include adaptive(1024) {
        font-size: 16px;
        line-height: 250%;
      }

      &:hover {
        color: $orange;
        text-decoration: none;
      }
    }
  }

  &__callback {
    @include adaptive(768) {
      border: none;
      font-size: 0;
      min-width: unset;
      padding: 0;
      margin-left: $level-6;
    }
    &::before {
      @include adaptive(768) {
        font-family: $icomoon-font-family;
        content: $icon-phone;
        font-size: 20px;
        color: $orange;
      }
    }
    &:hover {
      @include adaptive(768) {
        background: none;
      }
    }
    &:active {
      @include adaptive(768) {
        background: none;
        border: none;
      }
    }
  }

  &__burger {
    display: none;
    @include adaptive(1024) {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: -25px;
      top: 0;
      border: none;
      background: transparent;
      height: 100%;
      width: 56px;
      padding: 0;
      cursor: pointer;
      transition: $time;
      z-index: 100;
    }
    @include adaptive(600) {
      right: -18px;
    }

    & span {
      display: block;
      width: 24px;
      height: 2px;
      background: $gray-2;
      position: absolute;
      left: calc(50% - 12px);
      top: calc(50% - 1px);
    }

    &::before {
      content: '';
      display: block;
      width: 24px;
      height: 2px;
      background: $gray-2;
      transition: inherit;
      position: absolute;
      left: calc(50% - 12px);
      top: calc(50% - 9px);
    }

    &::after {
      content: '';
      display: block;
      width: 24px;
      height: 2px;
      background: $gray-2;
      transition: inherit;
      position: absolute;
      left: calc(50% - 12px);
      top: calc(50% + 7px);
    }

    &.burger-opened {
      background: $gray;
      z-index: 120;

      & span {
        display: none;
      }
      &::before {
        background: $black;
        transform: rotate(45deg);
        width: 26px;
        left: calc(50% - 11px);
        top: calc(50% - 1px);
      }
      &::after {
        background: $black;
        transform: rotate(-45deg);
        width: 26px;
        left: calc(50% - 11px);
        top: calc(50% - 1px);
      }
      & + .header__nav {
        transform: translateX(0);
      }
    }
  }
}
