@import "../helpers/variables";
@import "../helpers/mixins";

.form {
  &__text {
    font-size: 16px;
    margin-bottom: $level-4;
    @include adaptive(1600) {
      margin-bottom: $level-5;
    }
  }
  &__fields {
    width: 100%;
    margin-bottom: $level-4;
    @include adaptive(1600) {
      margin-bottom: $level-5;
    }
  }
  &__input {
    width: 100%;
    margin-bottom: 32px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__policy {
    font-size: 13px;
    margin-bottom: $level-4;
    @include adaptive(1600) {
      margin-bottom: $level-5;
    }
    & a {
      color: $black;
      font-weight: 400;
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
  &__button {
    height: 64px;
    @include adaptive(1600) {
      height: 48px;
    }
    @include adaptive(420) {
      width: 100%;
    }
  }
}
