@import "../helpers/variables";
@import "../helpers/mixins";

.breadcrumbs {
  margin: $level-4 0 $level-7;
  &__item {
    color: $black;
    font-weight: 400;
    display: inline-flex;
    &:hover {
      text-decoration: none;
    }
    &::after {
      content: $icon-arrow;
      font-family: $icomoon-font-family;
      width: 36px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $orange;
      transform: scale(-1, 1);
    }
    &._active {
      color: $orange;
      &:after {
        display: none;
      }
    }
  }
}
