@import "../helpers/variables";
@import "../helpers/mixins";

.catalog-inner {
  margin-bottom: $level-2;
  @include adaptive(1600) {
    margin-bottom: $level-3;
  }
  &__section {
    margin-bottom: $level-3;
    @include adaptive(1600) {
      margin-bottom: $level-4;
    }
  }
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    margin-top: $level-5;
  }
  &__item {
    width: calc(25% - 3px / 4);
    height: 250px;
    overflow: hidden;
    margin: 0 1px 1px 0;
    display: flex;
    @include adaptive(1600) {
      height: 200px;
    }
    @include adaptive(1200) {
      height: 160px;
    }
    @include adaptive(768) {
      width: calc(100% / 3 - 2px / 3);
    }
    @include adaptive(600) {
      width: calc(50% - 1px / 2);
      height: 190px;
    }
    @include adaptive(420) {
      height: 130px;
    }
    @include adaptive(360) {
      height: 110px;
    }
    @include adaptive(320) {
      width: 100%;
      height: 190px;
      margin-right: 0;
    }
    &:nth-child(4n) {
      margin-right: 0;
      @include adaptive(768) {
        margin-right: 1px;
      }
      @include adaptive(320) {
        margin-right: 0;
      }
    }
    &:nth-child(3n) {
      @include adaptive(768) {
        margin-right: 0;
      }
      @include adaptive(600) {
        margin-right: 1px;
      }
      @include adaptive(320) {
        margin-right: 0;
      }
    }
    &:nth-child(2n) {
      @include adaptive(600) {
        margin-right: 0;
      }
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: $dark-gray;
      opacity: .5;
      z-index: 1;
    }
    &_image {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &_info {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      width: 100%;
      height: 100%;
      padding: $level-5;
      z-index: 2;
      transition: $time;
      @include adaptive(1600) {
        padding: $level-6;
      }
      @include adaptive(1200) {
        padding: $level-6 $level-7;
      }
      @include adaptive(768) {
        padding: $level-5;
      }
      @include adaptive(600) {
        padding: $level-6;
      }
      @include adaptive(420) {
        padding: $level-6 $level-7;
      }
      &:hover {
        text-decoration: none;
        & .catalog-inner__item_name {
          min-height: calc(100% - 15px);
          transform: translateY(0);
        }
        & .catalog-inner__item_readmore {
          transform: translateY(0);
        }
      }
    }
    &_name {
      color: $white;
      display: flex;
      transition: $time;
      min-height: 27px;
      transform: translateY(15px);
      @include adaptive(1600) {
        min-height: 24px;
      }
    }
    &_readmore {
      color: $white;
      font-size: 14px;
      font-family: 'Oswald', sans-serif;
      line-height: 110%;
      letter-spacing: .1em;
      font-weight: 400;
      text-transform: uppercase;
      display: flex;
      align-items: center;
      transition: $time;
      transform: translateY(55px);
      & .icon {
        display: inline-flex;
        transform: rotate(180deg);
        margin: 0 18px;
        padding-top: 2px;
        font-size: 11px;
      }
    }
  }
}
