@import "../../helpers/variables";
@import "../../helpers/mixins";

.services {
  margin: $level-2 0;
  @include adaptive(1366) {
    margin: $level-3 0;
  }
  &__text {
    margin-bottom: $level-4;
  }
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    display: flex;
    width: 25%;
    height: 250px;
    @include adaptive(1600) {
      height: 200px;
    }
    @include adaptive(1200) {
      height: 160px;
    }
    @include adaptive(768) {
      width: 50%;
      height: 250px;
    }
    @include adaptive(600) {
      height: 200px;
    }
    @include adaptive(420) {
      height: 160px;
    }
    @include adaptive(375) {
      width: 100%;
      height: 200px;
    }
    &_image {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &_info {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: $level-5;
      color: $white;
      @include adaptive(1600) {
        padding: $level-6;
      }
      @include adaptive(1200) {
        padding: $level-6 $level-7;
      }
      @include adaptive(768) {
        padding: $level-5;
      }
      @include adaptive(600) {
        padding: $level-6;
      }
      @include adaptive(420) {
        padding: $level-6 $level-7;
      }
      &:hover {
        text-decoration: none;
        &::before {
          opacity: .5;
        }
      }
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $dark-gray;
        transition: $time;
      }
    }
    &_icon {
      font-size: 48px;
      @include adaptive(1200) {
        font-size: 36px;
      }
    }
  }
}
