@import "../helpers/variables";
@import "../helpers/mixins";

.footer {
  &__info {
    background: $gray;
    padding: $level-4 0;
    @include adaptive(1600) {
      padding: $level-6 0;
    }

    &_wrapper {
      display: flex;
      @include adaptive(768) {
        flex-direction: column;
        align-items: center;
        max-width: 100%;
        padding: 0;
      }
    }
  }

  &__logo {
    display: flex;
    align-items: flex-start;
    flex-shrink: 0;
    width: 470px;
    margin-right: 30px;
    user-select: none;
    @include adaptive(1600) {
      width: 325px;
      margin-right: 20px;
    }
    @include adaptive(1200) {
      width: 230px;
      margin-right: 10px;
    }
    @include adaptive(1024) {
      width: calc(20% - 16px);
      margin-right: 20px;
    }
    @include adaptive(768) {
      margin-right: 0;
      margin-bottom: $level-6;
      justify-content: center;
    }

    & img {
      width: 142px;
      @include adaptive(1600) {
        width: 130px;
      }
      @include adaptive(600) {
        max-height: 40px;
        width: auto;
      }
    }
  }

  &__main {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    @include adaptive(1024) {
      align-items: center;
    }
    @include adaptive(768) {
      flex-direction: column;
    }
  }

  &__nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: $level-4;
    @include adaptive(1600) {
      margin-bottom: $level-6;
    }
    @include adaptive(1024) {
      flex-direction: column;
      width: auto;
      margin-bottom: 0;
      align-items: center;
    }
    @include adaptive(768) {
      margin-bottom: $level-6;
    }

    &_link {
      color: $black;
      font-family: "Oswald", sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      letter-spacing: .1em;
      text-transform: uppercase;
      padding-left: 3px;
      @include adaptive(1600) {
        font-size: 14px;
      }
      @include adaptive(1200) {
        font-size: 12px;
      }
      @include adaptive(1024) {
        font-size: 14px;
        line-height: 200%;
      }

      &:hover {
        color: $orange;
        text-decoration: none;
      }
    }
  }

  &__socials {
    display: flex;
    @include adaptive(768) {
      margin-bottom: 32px;
    }

    &_link {
      color: $black;
      display: flex;
      margin-right: 37px;
      margin-bottom: 7px;
      @include adaptive(1600) {
        margin-right: 29px;
      }
      @include adaptive(1200) {
        margin-right: 25px;
      }
      @include adaptive(1024) {
        margin-bottom: 0;
      }

      &:hover {
        text-decoration: none;
        color: $orange;
      }

      &:active {
        color: $orange;
      }

      &.icon {
        font-size: 20px;
        @include adaptive(1600) {
          font-size: 18px;
        }
        @include adaptive(1200) {
          font-size: 16px;
        }
      }

      &:last-child {
        @include adaptive(1024) {
          margin-right: 0;
        }
      }
    }
  }

  &__contacts {
    display: flex;
    flex-wrap: wrap;
    text-align: right;
    max-width: 508px;
    @include adaptive(1600) {
      max-width: 340px;
    }
    @include adaptive(1024) {
      text-align: center;
      max-width: unset;
      flex-direction: column;
    }
    @include adaptive(768) {
      width: 100%;
      border-top: 1px solid #d2d2d8;
      padding-top: $level-6;
      margin-bottom: 16px;
    }

    &_time {
      width: 50%;
      @include adaptive(1024) {
        width: 100%;
        order: 3;
      }
    }

    &_address {
      width: 50%;
      order: 2;
      @include adaptive(1024) {
        width: 100%;
        order: 4;
      }
    }

    &_phone {
      display: block;
      width: 50%;
      color: $black;
      @include adaptive(1024) {
        width: 100%;
        order: 1;
      }
    }

    &_email {
      display: block;
      width: 50%;
      order: 2;
      font-weight: 400;
      color: $black;
      @include adaptive(1024) {
        width: 100%;
      }
    }
  }

  &__copyrights {
    background: $gray-2;
    padding: $level-6 0;
    @include adaptive(1600) {
      padding: $level-7 0;
    }

    &_wrapper {
      display: flex;
      justify-content: space-between;
    }

    &_policy {
      font-weight: 400;
      color: $black;
    }
  }
}
