@import "../../helpers/variables";
@import "../../helpers/mixins";

.about {
  margin: $level-2 0;
  @include adaptive(1366) {
    margin: $level-3 0;
  }
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__info {
    width: 845px;
    flex-shrink: 0;
    @include adaptive(1600) {
      width: 680px;
    }
    @include adaptive(1200) {
      width: 550px;
    }
    @include adaptive(768) {
      width: calc(75% - 15px);
    }
    @include adaptive(600) {
      width: 100%;
    }
    &_header {
      margin-bottom: $level-4;
    }
  }
  &__image {
    width: 500px;
    flex-shrink: 0;
    mask: url("../images/goodwork.svg") top left / contain;
    @include adaptive(1600) {
      width: 400px;
    }
    @include adaptive(1200) {
      width: 320px;
    }
    @include adaptive(768) {
      width: calc(25% - 15px)
    }
    @include adaptive(600) {
      width: 100px;
      position: absolute;
      right: 18px;
      top: 0;
    }
  }
  &__advantages {
    width: 100%;
    margin: $level-3 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    &_item {
      width: 220px;
      display: flex;
      flex-direction: column;
      color: $gray-2;
      transition: $time;
      @include adaptive(1600) {
        width: 180px;
      }
      @include adaptive(1200) {
        width: 150px;
      }
      @include adaptive(768) {
        width: calc(100% / 3 - 20px);
        margin-bottom: $level-6;
      }
      @include adaptive(600) {
        width: calc(50% - 15px);
      }
      @include adaptive(375) {
        width: 100%;
        align-items: center;
        text-align: center;
      }
      &:hover {
        color: $dark-gray;
        & .icon {
          color: $orange;
        }
      }
    }
    &_icon {
      font-size: 48px;
      margin-bottom: $level-6;
      transition: $time;
    }
    &_name {
      @include adaptive(375) {
        max-width: 80%;
      }
    }
  }
}
