@import "../../helpers/variables";
@import "../../helpers/mixins";

.catalog {
  margin: -116px 0 $level-2;
  z-index: 10;
  @include adaptive(1600) {
    margin: -95px 0 $level-3;
  }
  @include adaptive(1024) {
    margin: $level-4 0;
  }
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
  }
  &__section {
    display: flex;
    width: calc(100% / 6);
    height: 450px;
    overflow: hidden;
    @include adaptive(1600) {
      height: 360px;
    }
    @include adaptive(1200) {
      height: 290px;
    }
    @include adaptive(768) {
      width: calc(100% / 3);
      height: 450px;
    }
    @include adaptive(600) {
      width: 50%;
      height: 360px;
    }
    @include adaptive(320) {
      width: 100%;
    }
    &_image {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &_info {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      height: 100%;
      padding: $level-5;
      @include adaptive(1600) {
        padding: $level-6;
      }
      @include adaptive(1200) {
        padding: $level-6 $level-7;
      }
      @include adaptive(768) {
        padding: $level-5;
      }
      @include adaptive(600) {
        padding: $level-6;
      }
      @include adaptive(420) {
        padding: $level-6 $level-7;
      }
      &:hover {
        text-decoration: none;
        &::before {
          opacity: .6;
        }
        & .catalog__section_readmore {
          transform: translateX(0);
        }
      }
      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 200px;
        background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
        transition: $time;
        opacity: 0;
      }
    }
    &_name {
      color: $black;
    }
    &_readmore {
      color: $white;
      font-size: 14px;
      font-family: 'Oswald', sans-serif;
      line-height: 110%;
      letter-spacing: .1em;
      font-weight: 400;
      text-transform: uppercase;
      display: flex;
      align-items: center;
      transform: translateX(-100%);
      transition: $time;
      & .icon {
        display: inline-flex;
        transform: rotate(180deg);
        margin: 0 18px;
        padding-top: 2px;
        font-size: 11px;
      }
    }
  }
}
