@import "../helpers/variables";
@import "../helpers/mixins";

.button {
  font-family: 'Oswald', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  letter-spacing: .1em;
  text-transform: uppercase;
  color: $black;
  transition: $time;
  border: none;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 171px;
  user-select: none;
  cursor: pointer;
  @include adaptive(1600) {
    font-size: 12px;
    height: 36px;
    min-width: 160px;
  }
  @include adaptive(1024) {
    min-width: 137px;
  }
  &:hover {
    text-decoration: none;
  }
  &-transparent {
    border: 1px solid $black;
    background: transparent;

    &:hover {
      color: $white;
      background: $black;
    }

    &:active {
      color: $white;
      background: $dark-gray;
      border: 1px solid $dark-gray;
    }
  }
  &-orange {
    background: $orange;
    &:hover {
      background: $white;
    }
    &:active {
      background: $white;
      color: $dark-gray;
    }
  }
  &-black {
    background: $black;
    color: $white;
    &:hover {
      background: $white;
      color: $black;
    }
    &:active {
      background: $white;
      color: $dark-gray;
    }
  }
  &-control {
    min-width: unset;
    padding: 0 30px;
    position: relative;
    width: auto;
    & .icon {
      font-size: 11px;
      position: absolute;
      top: 50%;
    }
    &-prev {
      & .icon {
        left: 0;
        transform: translateY(-50%);
      }
    }
    &-next {
      margin-right: 15px;
      @include adaptive(1600) {
        margin-right: 10px;
      }
      @include adaptive(1024) {
        margin-right: 25px;
      }
      & .icon {
        right: 0;
        transform: translateY(-50%) scale(-1, 1);
      }
    }
    &::after {
      display: none;
    }
  }
  &-readmore {
    color: $orange;
    padding-right: 30px;
    width: fit-content;
    min-width: unset;
    height: auto;
    & .icon {
      color: $orange;
      font-size: 11px;
      position: absolute;
      right: 0;
      top: 6px;
      transform: translateY(-50%) scale(-1, 1);
    }
    &:hover {
      color: $black;
    }
    &:active {
      color: $dark-gray;
    }
  }
}
