@import "../helpers/variables";
@import "../helpers/mixins";

.popup {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 130;
  background: $dark-gray-transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: $time;
  opacity: 0;
  pointer-events: none;
  &._visible {
    opacity: 1;
    pointer-events: initial;
  }
  &__wrapper {
    background: $white;
    padding: $level-3;
    width: 470px;
    @include adaptive(1600) {
      width: 380px;
      padding: $level-4;
    }
    @include adaptive(600) {
      width: 100vw;
    }
  }
  &__form {
    width: 100%;
    &_button {
      width: 171px;
      &:hover {
        background: $orange;
        color: $black;
      }
      &:active {
        color: $dark-gray;
      }
    }
  }
  &__close {
    width: $level-3;
    height: $level-3;
    background: $orange;
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 0;
    transform: translateX(100%);
    @include adaptive(600) {
      width: $level-4;
      height: $level-4;
      background: transparent;
      transform: translateX(0);
    }
    &_inner {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      &::before {
        content: '';
        width: 32px;
        height: 2px;
        display: block;
        background: $black;
        transform: rotate(45deg);
        transform-origin: center;
        transition: $time;
        position: absolute;
      }
      &::after {
        content: '';
        width: 32px;
        height: 2px;
        display: block;
        background: $black;
        transform: rotate(-45deg);
        transform-origin: center;
        transition: $time;
        position: absolute;
      }
    }
    &:hover {
      & .popup__close_inner {
        &::before {
          transform: rotate(225deg);
        }
        &::after {
          transform: rotate(135deg);
        }
      }
    }
  }
  &-callback {
    & .popup__wrapper {
      height: 588px;
      @include adaptive(1600) {
        height: 491px;
      }
      @include adaptive(600) {
        height: 100vh;
      }
    }
  }
}
