@include fonts("Oswald", "Oswald", 500, normal);
@include fonts("Oswald", "Oswald", 400, normal);
@include fonts("Oswald", "Oswald", 300, normal);
@include fonts("PT Sans", "PTSans", 700, normal);
@include fonts("PT Sans", "PTSans", 400, normal);

@import "../helpers/variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?rlzx12') format('truetype'),
  url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?rlzx12') format('woff'),
  url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?rlzx12##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-long-arrow {
  &:before {
    content: $icon-long-arrow;
